.mainContent {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.mainContent div {
  display: flex;
}
.mainContent a {
  word-break: break-all;
}
.mainContent div a {
  color: #023e8a;
}
@media only screen and (max-width: 768px) {
  .mainContent div a {
    font-size: 1rem;
  }
}
