.app {
  height: 90vh;
  width: 90vw;
  display: flex;
  gap: 2rem;
}
.main {
  flex: 1;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  padding: 10px 20px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 768px) {
  .app {
    height: 80dvh;
    margin-top: 10px;
  }
}
