.skillscontainer,
.timelinecontainer {
  display: flex;
  gap: 2%;
  flex-wrap: wrap;
}
.mainContent {
  flex: 1;
  overflow-y: auto;
}

.txt,
.img {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}
.img img {
  border-radius: 50%;
}
.title {
  margin-bottom: 15px;
}
/*====================================================*/
.summary p {
  letter-spacing: 0.5px;
  padding-right: 20px;
}
.yt {
  font-weight: 600;
  font-size: 1.1rem;
  color: #0077b6;
}
.skillscontainer .skill {
  width: 30%;
  margin-bottom: 15px;
}
.skillscontainer .skill h3 {
  font-weight: 500;
}
.skillscontainer .skill .progress {
  background: #caf0f8;
  font-size: 14px;
  border-radius: 30px;
  margin-top: 5px;
}
.skillscontainer .skill .progress span {
  background: linear-gradient(to right top, #ade8f4, #90e0ef);
  display: inline-block;
  border-radius: 30px;
  padding: 0 5px;
  font-weight: 500;
  color: #023e8a;
}
/*====================================================*/
.timeline {
  width: 48%;
  margin-bottom: 15px;
  padding: 10px;
  box-shadow: 1.95px 1.95px 2.6px rgba(0, 0, 0, 0.1);
}
.timeline p:first-child {
  text-transform: capitalize;
  font-size: 1.2rem;
}
.timeline p:first-child span:first-child {
  color: #03045e;
  font-weight: 700;
}
.timeline p:first-child span:last-child {
  color: #023e8a;
  font-weight: 500;
}
.timeline .year {
  background-color: #03045e;
  border-radius: 50px;
  color: #ffffff;
  font-size: 0.75rem;
  letter-spacing: 1px;
  font-weight: 500;
  margin: 5px 0;
  padding: 2px 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
@media only screen and (max-width: 650px) {
  .mainContent .skillscontainer .skill {
    width: 47%;
  }
}
@media only screen and (max-width: 430px) {
  .mainContent .skillscontainer .skill,
  .mainContent .timelinecontainer .timeline {
    width: 95%;
  }
}
