.carsoule {
  width: 60%;
  display: flex;
  margin: 20px auto;
  justify-content: space-between;
  align-items: center;
}
.carsoule span {
  width: 2rem;
  height: 2rem;
  display: inline-block;
  cursor: pointer;
  filter: invert(1) grayscale(100);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.carsoule span:first-of-type {
  background-image: url(./../../assessts/lft.svg);
}
.carsoule span:last-of-type {
  background-image: url(./../../assessts/rit.svg);
}
.carsoule h3 {
  width: fit-content;
  background-color: rgba(180, 180, 180, 0.5);
  border-radius: 5px;
  padding: 0 10%;
  text-align: center;
  cursor: pointer;
}
.player {
  width: 80%;
  height: 500px;
  margin: auto;
}
@media only screen and (max-width: 768px) {
  .carsoule {
    width: 90%;
  }
  .player {
    width: 100%;
    height: 300px;
  }
}
