.nav {
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.5)
  );
  width: 90vw;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  padding: 15px;
  border-radius: 20px;
}
.nav ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.nav li {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav li a {
  display: block;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  border-radius: 10px;
  transition: 0.3s;
  padding: 7px 7px 0 7px;
}
