.mainContent {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 3%;
}
.projcetCard {
  width: 30%;
  height: fit-content;
  margin-bottom: 20px;
  text-align: center;
  box-shadow: 1.95px 1.95px 2.6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.projcetCard a {
  display: inline-block;
}
.projcetCard h3 {
  margin: 5px 0;
  transition: all 0.3s;
}
.projcetCard h3 span {
  position: relative;
  left: 0;
  transition: all 0.3s;
}
.projcetCard h3:hover {
  transform: scale(1.05);
}
.projcetCard h3:hover span {
  position: relative;
  left: 3px;
}
@media only screen and (max-width: 1024px) {
  .mainContent .projcetCard {
    width: 46%;
  }
}
@media only screen and (max-width: 500px) {
  .mainContent .projcetCard {
    width: 100%;
  }
}
