.maincontent {
  overflow-y: auto;
}
.btns {
  width: fit-content;
  display: flex;
  gap: 30px;
  margin: 20px auto;
}
.btns button,
a {
  color: #001d3d;
  font-weight: bold;
  font-size: 0.9;
}
.btns button {
  cursor: pointer;
  padding: 10px 25px;
  background-color: #ffffff66;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  border-radius: 10px;
  border: none;
  transition: 0.3s;
}
