.aside {
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.5)
  );
  width: 20%;
  padding: 20px 10px;
  border-radius: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info img {
  display: inline-block;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.24);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.info h2 {
  margin: 10px;
  font-family: "Kaushan Script", cursive;
}
.info p:first-of-type {
  margin: 5px 0 5px 0;
}
.info p {
  font-family: "Dosis", sans-serif;
  line-height: normal;
  font-weight: bold;
  opacity: 0.7;
}
.aside .navigation {
  width: 100%;
  flex: 1;
  display: flex;
  align-items: center;
}
.aside ul {
  list-style: none;
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.aside ul li {
  width: 60%;
}
.aside ul li a {
  padding: 5px 0;
  display: block;
  background-color: rgba(255, 255, 255, 0.4);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  border-radius: 10px;
  font-size: 1.2rem;
  color: #001d3d;
  font-weight: 500;
  transition: 0.3s;
}
@media only screen and (max-width: 885px) {
  .aside ul li a {
    font-size: 1rem;
  }
  .aside .info img {
    border: 3px solid rgba(255, 255, 255, 0.24) !important;
  }
}
